.school-students-dialog {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 90vh;
  background: #fff;

  .dialog-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 24px;
    border-bottom: 1px solid #e0e0e0;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;

    .dialog-title {
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .close-btn {
      color: white;
      
      &:hover {
        background: rgba(255, 255, 255, 0.2);
      }
    }
  }

  .school-info-section {
    padding: 20px 24px;
    border-bottom: 1px solid #e0e0e0;
    background: #f9f9f9;

    .school-header {
      display: flex;
      gap: 16px;
      align-items: flex-start;

      .school-logo {
        width: 80px;
        height: 80px;
        border-radius: 8px;
        overflow: hidden;
        background: #e0e0e0;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .school-details {
        flex: 1;

        h3 {
          margin: 0 0 8px 0;
          font-size: 16px;
          font-weight: 600;
          color: #333;
        }

        .school-desc {
          margin: 0 0 12px 0;
          font-size: 13px;
          color: #666;
          line-height: 1.4;
        }

        .school-meta {
          display: flex;
          gap: 16px;

          .meta-item {
            display: flex;
            align-items: center;
            gap: 6px;
            font-size: 13px;
            color: #555;

            mat-icon {
              font-size: 18px;
              width: 18px;
              height: 18px;
              line-height: 18px;
              color: #667eea;
            }
          }
        }
      }
    }
  }

  .course-select-section {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 24px;
    background: #e8f5e9;
    border-bottom: 1px solid #c8e6c9;

    .select-label {
      display: flex;
      align-items: center;
      gap: 8px;
      font-size: 14px;
      font-weight: 500;
      color: #2e7d32;
      white-space: nowrap;

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
      }
    }

    .course-select {
      flex: 1;
      max-width: 400px;

      ::ng-deep {
        .mat-mdc-form-field-subscript-wrapper {
          display: none;
        }
        
        .mat-mdc-text-field-wrapper {
          background: white;
        }
      }
    }
  }

  .students-list-section {
    flex: 1;
    overflow-y: auto;
    padding: 20px 24px;

    .section-title {
      margin: 0 0 16px 0;
      font-size: 15px;
      font-weight: 600;
      color: #333;
      display: flex;
      align-items: center;
      gap: 8px;
    }

    .empty-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 40px 20px;
      color: #999;

      .empty-icon {
        font-size: 48px;
        width: 48px;
        height: 48px;
        line-height: 48px;
        margin-bottom: 16px;
        color: #ccc;
      }

      p {
        margin: 0;
        font-size: 14px;
      }
    }

    .students-table-container {
      overflow-x: auto;
      border-radius: 8px;
      border: 1px solid #e0e0e0;

      .students-table {
        width: 100%;
        border-collapse: collapse;
        font-size: 13px;

        thead {
          background: #f5f5f5;
          border-bottom: 2px solid #e0e0e0;

          th {
            padding: 12px;
            text-align: left;
            font-weight: 600;
            color: #333;
            white-space: nowrap;

            &.name-header {
              width: 20%;
            }

            &.email-header {
              width: 25%;
            }

            &.phone-header {
              width: 20%;
            }

            &.status-header {
              width: 15%;
            }

            &.enrolled-header {
              width: 15%;
            }

            &.actions-header {
              width: 10%;
              text-align: center;
            }
          }
        }

        tbody {
          tr {
            border-bottom: 1px solid #f0f0f0;
            transition: background-color 0.2s ease;

            &:hover {
              background: #fafafa;
            }

            td {
              padding: 12px;
              color: #555;

              &.name-cell {
                .student-name {
                  display: flex;
                  align-items: center;
                  gap: 8px;
                  font-weight: 500;
                  color: #333;

                  .student-icon {
                    font-size: 18px;
                    width: 18px;
                    height: 18px;
                    line-height: 18px;
                    color: #667eea;
                    flex-shrink: 0;
                  }
                }
              }

              &.email-cell {
                word-break: break-all;
                font-size: 12px;
              }

              &.phone-cell {
                font-size: 12px;
              }

              &.status-cell {
                .status-badge {
                  display: inline-block;
                  padding: 4px 12px;
                  border-radius: 12px;
                  font-size: 11px;
                  font-weight: 600;
                  text-align: center;
                  white-space: nowrap;

                  &.status-active {
                    background: #e8f5e9;
                    color: #2e7d32;
                  }

                  &.status-pending {
                    background: #fff3e0;
                    color: #e65100;
                  }

                  &.status-graduated {
                    background: #e3f2fd;
                    color: #1565c0;
                  }

                  &.status-withdrawn {
                    background: #f3e5f5;
                    color: #6a1b9a;
                  }

                  &.status-expelled {
                    background: #ffebee;
                    color: #c62828;
                  }
                }
              }

              &.enrolled-cell {
                font-size: 12px;
                color: #999;
              }

              &.actions-cell {
                text-align: center;

                button {
                  mat-icon {
                    color: #4caf50;
                  }

                  &:disabled {
                    mat-icon {
                      color: #ccc;
                    }
                  }

                  &:hover:not(:disabled) {
                    background: rgba(76, 175, 80, 0.1);
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .dialog-footer {
    display: flex;
    justify-content: flex-end;
    padding: 16px 24px;
    border-top: 1px solid #e0e0e0;
    background: #fafafa;
    gap: 8px;

    button {
      min-width: 100px;
    }
  }
}
